@font-face {
  font-family: "Poppins";
  src: local("Poppins"), url("../fonts/Poppins/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins/Poppins-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: local("Poppins"), url("../fonts/Poppins/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins/Poppins-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: local("Poppins"), url("../fonts/Poppins/Poppins-SemiBold.woff2") format("woff2"), url("../fonts/Poppins/Poppins-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: local("Poppins"), url("../fonts/Poppins/Poppins-Bold.woff2") format("woff2"), url("../fonts/Poppins/Poppins-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: local("Poppins"), url("../fonts/Poppins/Poppins-ExtraBold.woff2") format("woff2"), url("../fonts/Poppins/Poppins-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
}
.intro, .site-nav, .header__nav {
  display: flex;
  align-items: center;
}

body {
  margin: 0;
  background-color: #fff;
  font-family: "Poppins", sans-serif;
  font-size: 25px;
  line-height: 1.5;
  font-weight: 500;
  color: #486958;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input, textarea, select, button {
  font-family: inherit;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.text-uppercase {
  text-transform: uppercase;
}

/* Focus-element
================*/
:focus {
  outline: 2px solid #404242;
}

:focus:not(:focus-visible) {
  outline: 0;
}

:focus-visible {
  outline: 2px solid #404242;
}

/* Container
================*/
.container {
  width: 100%;
  max-width: 1642px;
  padding: 0 15px;
  margin: 0 auto;
}

/* Components */
.header {
  width: 100%;
  background-color: transparent;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}
.header__nav {
  justify-content: space-between;
  height: 140px;
}

.logo {
  margin-right: 110px;
  color: #58A9A5;
  transition: opacity 0.2s linear;
}
.logo:hover {
  opacity: 0.6;
}
.logo__img {
  display: inline-block;
  width: 45px;
  height: 53px;
  vertical-align: middle;
  margin: 0 3px 9px -4px;
}
.logo__img-text {
  font-size: 35px;
  font-weight: 800;
}

.site-nav {
  margin-right: 20px;
  color: #404242;
}
.site-nav__item:not(:last-child) {
  margin-right: 70px;
}
.site-nav__link {
  padding: 8px;
  font-weight: 400;
  position: relative;
}
.site-nav__link:before {
  content: "";
  width: 55%;
  height: 6px;
  position: absolute;
  bottom: 0;
  left: 50%;
  background-color: #FEC62D;
  border-radius: 21px;
  transition: all 0.2s;
  transform: scaleX(0) translateX(-50%);
  transform-origin: left;
  opacity: 0;
}
.site-nav__link:hover:before {
  transform: scaleX(1) translateX(-50%);
  opacity: 1;
}
.site-nav__link:hover {
  color: #58A9A5;
}

.burger {
  display: none;
}

.btn {
  display: inline-block;
  vertical-align: middle;
  padding: 25px 88px;
  font-family: "Poppins", sans-serif;
  font-size: 25px;
  line-height: 1.2;
  color: #fff;
  font-weight: 400;
  letter-spacing: normal;
  cursor: pointer;
  background-color: #58A9A5;
  border: 0;
  transition: background-color 0.2s ease-in;
}
.btn:hover {
  background-color: #468885;
}
.btn--second {
  border-radius: 15px;
}
.btn--primary {
  border-radius: 20px 20px 20px 0px;
}

.intro {
  min-height: 100vh;
  height: 100vh;
  padding-top: 140px;
}
.intro__wrapper {
  margin-top: 30px;
  position: relative;
}
.intro__content {
  width: 100%;
  max-width: 740px;
}
.intro__img {
  position: absolute;
  top: -6px;
  right: 19px;
  z-index: 2;
}
.intro__blob {
  width: 834px;
  height: 782px;
  position: absolute;
  top: -48px;
  right: -36px;
  z-index: 1;
}
.intro__title {
  margin-bottom: 50px;
  font-size: 70px;
  font-weight: 800;
  color: #1C2323;
}
.intro__description {
  margin-bottom: 50px;
  font-size: 30px;
  font-weight: 400;
}